import { TAG_AUDIO_SUPPORTED_EXT } from './constants';
import { HttpError } from './httpError';

export function sanitizeFolderName(folderName: string) {
  return (
    folderName
      /* Remove Line-Breaks */
      .replace(/((\r\n)|\r|\n)+/gm, '')
      /* Normalize Windows directory separators */
      .replace(/\\/gm, '/')
      /* Remove empty or ._- only dirs */
      .replace(/(\/|^)[\._\-\/]+\//g, '/')
  );
}

export default function sanitizeFileName(fileName: string) {
  const ext = fileName.split('.').pop();

  if (!ext) {
    throw new HttpError(`${fileName} is missing an extension`, 400);
  }

  if (!TAG_AUDIO_SUPPORTED_EXT.includes(`.${ext.toLowerCase()}`)) {
    throw new HttpError(`${ext} extension is not supported`, 415);
  }

  return `${sanitizeFolderName(
    fileName.substring(0, fileName.length - (ext.length + 1)),
  )}.${ext.toLowerCase()}`;
}
