import type {
  Created as CreatedInt,
  Uploaded as UploadedInt,
  Submitted as SubmittedInt,
  Resolved as ResolvedInt,
  NotFound as NotFoundInt,
  Found as FoundInt,
  Done as DoneInt,
  Error as ErrorInt,
  External,
  SomeExternalAudio,
} from './Audio';
import { STATUS } from './constants';

export type Created = External<CreatedInt>;
export type Uploaded = External<UploadedInt>;
export type Submitted = External<SubmittedInt>;
export type Resolved = External<ResolvedInt>;
export type NotFound = External<NotFoundInt>;
export type Found = External<FoundInt>;
export type Done = External<DoneInt>;
export type Error = External<ErrorInt>;
export type File = SomeExternalAudio;

export function isCreated(file: File): file is Created;
export function isCreated(
  file: File,
  exact: false,
): file is Created | Uploaded | Submitted | NotFound | Resolved | Found | Done;
export function isCreated(file: File, exact: boolean = true): boolean {
  return exact
    ? file.status === STATUS.CREATED
    : [
        STATUS.CREATED,
        STATUS.UPLOADED,
        STATUS.SUBMITTED,
        STATUS.NOT_FOUND,
        STATUS.RESOLVED,
        STATUS.FOUND,
        STATUS.DONE,
      ].includes(file.status);
}
export function isUploaded(file: File): file is Uploaded;
export function isUploaded(
  file: File,
  exact: false,
): file is Uploaded | Submitted | NotFound | Resolved | Found | Done;
export function isUploaded(file: File, exact: boolean = true): boolean {
  return exact
    ? file.status === STATUS.UPLOADED
    : [
        STATUS.UPLOADED,
        STATUS.SUBMITTED,
        STATUS.NOT_FOUND,
        STATUS.RESOLVED,
        STATUS.FOUND,
        STATUS.DONE,
      ].includes(file.status);
}
export function isSubmitted(file: File): file is Submitted;
export function isSubmitted(
  file: File,
  exact: false,
): file is Submitted | NotFound | Resolved | Found | Done;
export function isSubmitted(file: File, exact: boolean = true): boolean {
  return exact
    ? file.status === STATUS.SUBMITTED
    : [
        STATUS.SUBMITTED,
        STATUS.NOT_FOUND,
        STATUS.RESOLVED,
        STATUS.FOUND,
        STATUS.DONE,
      ].includes(file.status);
}
export function isResolved(file: File): file is Resolved;
export function isResolved(
  file: File,
  exact: false,
): file is Resolved | Found | Done;
export function isResolved(file: File, exact: boolean = true): boolean {
  return exact
    ? file.status === STATUS.RESOLVED
    : [STATUS.RESOLVED, STATUS.FOUND, STATUS.DONE].includes(file.status);
}
export function isNotFound(file: File): file is NotFound {
  return file.status === STATUS.NOT_FOUND;
}
export function isFound(file: File): file is Found;
export function isFound(file: File, exact: false): file is Found | Done;
export function isFound(file: File, exact: boolean = true): boolean {
  return exact
    ? file.status === STATUS.FOUND
    : [STATUS.FOUND, STATUS.DONE].includes(file.status);
}
export function isDone(file: File): file is Done {
  return file.status === STATUS.DONE;
}
export function isError(file: File): file is Error {
  return file.status === STATUS.ERROR;
}
