import type { FolderList, File as SingleFile, FileList } from '../shared/Audio';
import sanitizeFileName, {
  sanitizeFolderName,
} from '../shared/sanitizeFileName';
import { DOWNLOAD_OPTIONS } from '../shared/constants';

interface Config {
  token: string;
  apiUrl: string;
}

type UserConfig = Pick<Config, 'token'> & Partial<Omit<Config, 'token'>>;

export * from '../shared/is';
export { STATUS } from '../shared/constants';
export type { UserConfig as Config };

export default class MusicubeFingerprintingWeb {
  config: Config;
  constructor({
    token,
    apiUrl = 'https://w9dxkmzqu6.execute-api.eu-central-1.amazonaws.com',
  }: UserConfig) {
    this.config = { token, apiUrl };
  }
  async upload(
    file: File & { overwrite?: boolean },
    progress: (uploaded: number, total: number) => void = () => {},
  ): Promise<string> {
    const sanitizedName = sanitizeFileName(file.name);
    const res = await fetch(
      `${this.config.apiUrl.replace(/\/$/, '')}/upload/${encodeURIComponent(
        sanitizedName,
      )}`,
      {
        headers: {
          'x-mc-overwrite': file.overwrite ? 'yes' : 'no',
          authorization: `Bearer ${this.config.token}`,
        },
      },
    );

    if (res.status !== 200) {
      throw new Error(await res.text());
    }

    const { fields, url, name } = await res.json();
    const form = new FormData();
    const xhr = new XMLHttpRequest();

    for (const key in fields) {
      form.append(key, fields[key]);
    }
    form.append('file', file);

    return new Promise<string>((res, rej) => {
      xhr.onload = () => {
        if (xhr.status === 204) {
          res(name);
        } else {
          rej(new Error('Upload Failed'));
        }
      };
      xhr.onerror = rej;
      xhr.upload.addEventListener(
        'progress',
        (evt) => {
          progress(evt.loaded, evt.total);
        },
        false,
      );

      xhr.open('POST', url, true);
      xhr.send(form);
    });
  }
  async folders() {
    const res = await fetch(`${this.config.apiUrl.replace(/\/$/, '')}/audio/`, {
      headers: {
        authorization: `Bearer ${this.config.token}`,
      },
    });

    if (res.status !== 200) {
      throw new Error(await res.text());
    }

    return ((await res.json()) as FolderList).folders;
  }
  async files(folder: string) {
    if (sanitizeFolderName(folder) !== folder) {
      throw new Error(`Invalid folder name: ${folder}`);
    }

    const res = await fetch(
      `${this.config.apiUrl.replace(/\/$/, '')}/audio/${encodeURIComponent(
        folder,
      )}/`,
      {
        headers: {
          authorization: `Bearer ${this.config.token}`,
        },
      },
    );

    if (res.status !== 200) {
      throw new Error(await res.text());
    }

    return ((await res.json()) as FileList).files;
  }
  async file(file: string) {
    if (
      sanitizeFileName(file) !== file ||
      file.split('/').filter((t) => t && t.length).length !== 2
    ) {
      throw new Error(`Invalid file name: ${file}`);
    }

    const res = await fetch(
      `${this.config.apiUrl.replace(/\/$/, '')}/audio/${encodeURIComponent(
        file,
      )}`,
      {
        headers: {
          authorization: `Bearer ${this.config.token}`,
        },
      },
    );

    if (res.status !== 200) {
      throw new Error(await res.text());
    }

    return ((await res.json()) as SingleFile).file;
  }
  async delete(file: string) {
    if (
      sanitizeFileName(file) !== file ||
      file.split('/').filter((t) => t && t.length).length !== 2
    ) {
      throw new Error(`Invalid file name: ${file}`);
    }

    const res = await fetch(
      `${this.config.apiUrl.replace(/\/$/, '')}/audio/${encodeURIComponent(
        file,
      )}`,
      {
        method: 'DELETE',
        headers: {
          authorization: `Bearer ${this.config.token}`,
        },
      },
    );

    if (res.status !== 204) {
      throw new Error(await res.text());
    }

    return;
  }
  async resubmit(file: string) {
    if (
      sanitizeFileName(file) !== file ||
      file.split('/').filter((t) => t && t.length).length !== 2
    ) {
      throw new Error(`Invalid file name: ${file}`);
    }

    const res = await fetch(
      `${this.config.apiUrl.replace(/\/$/, '')}/audio/${encodeURIComponent(
        file,
      )}`,
      {
        method: 'POST',
        headers: {
          'x-mc-resubmit': 'yes',
          authorization: `Bearer ${this.config.token}`,
        },
      },
    );

    if (res.status !== 204) {
      throw new Error(await res.text());
    }

    return;
  }
  async downloadLink(file: string) {
    if (
      sanitizeFileName(file) !== file ||
      file.split('/').filter((t) => t && t.length).length !== 2
    ) {
      throw new Error(`Invalid file name: ${file}`);
    }

    const res = await fetch(
      `${this.config.apiUrl.replace(/\/$/, '')}/audio/${encodeURIComponent(
        file,
      )}`,
      {
        headers: {
          'x-mc-download': DOWNLOAD_OPTIONS.URL,
          authorization: `Bearer ${this.config.token}`,
        },
      },
    );

    if (res.status !== 200) {
      throw new Error(await res.text());
    }

    return res.text();
  }
}
